@font-face {
    font-family: 555;
    src: url(./karla/Karla-Regular.ttf);
}.footer{
    margin-top: 100px;
    width: 100%;
    ul{
        width: 100%;
        list-style: none;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;
        padding: 0;
        li:nth-child(1){
            width: 30%;
        }
        li:nth-child(2){
            width: 20%;
        }
        li:nth-child(3){
            width: 20%;
        }
        li:nth-child(4){
            width: 26%;
        }
        li{
           width: 25%;
            text-align: left;
            .blok .footer #h3{
                font-size: 12px;
                text-align: left;
                font-weight: 400;
                padding: 0;
                margin: 0;
                letter-spacing: 2px;
                line-height: 18px;
                text-transform: uppercase;
                text-align: left;
                font-family:555;
                img{
                    margin: 0;
                    text-align: left;
                    padding: 0;
                }
            }
            #ul1{
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
               padding-top: 22px;
                li{
                    width: 100%;
                   
                    p{
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 22px;
                        color: #1d1f2e9d;
                        padding-top: 10px;
                        font-family:555;
                    }
                    h6{
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 22px;
                        color:#1d1f2e36 ;
                        padding-top: 80px;
                        font-family:555;
                    }
                }
            }
            #ul2, #ul3, #ul4{
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                padding-top: 22px;
                li{
                    width: 100%;
                    padding-top: 10px;
                   
                    a{
                        text-decoration: none;
                        font-size: 13px;
                        font-weight: 400;
                        line-height: 22px;
                        color: #1d1f2e9d;
                        padding-top: 10px;
                       font-family: 555;
                    }
                    a:hover{
                        color: blueviolet;
                    }
                    h1{
                        font-size: 23px;
                        font-weight: 400;
                        line-height: 36px;
                        color:#1D1F2E ;
                        padding-top: 16px;
                        font-family:555;
                    }
                    h6{
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 22px;
                        color:#1d1f2e36 ;
                        padding-top: 48px;
                        font-family:555;
                    }
                    
                }
            }
            
            }
        }
    }
@media screen and (max-width:768px) {
    .footer ul li #ul4 li h1{
        font-size: 15px;
        
    }
    .footer{
        margin: auto;
        margin-top: 40px;
    }
    
}
@media screen and (max-width:576px) {
    .footer ul{
        flex-direction: column;
        text-align: center;
        margin-top: 15px;
    }
   .footer ul li:nth-child(1){
        width: 50%;
        margin: auto;
        margin-top: 10px;
    }
    .footer ul li:nth-child(2){
        width: 50%;
        margin: auto;
        margin-top: 10px;
    }
    .footer ul li:nth-child(3){
        width: 50%;
        margin: auto;
        margin-top: 10px;
    }
    .footer ul li:nth-child(4){
        width: 56%;
        margin: auto;
        margin-top: 10px;
    }
    .footer ul li #ul1 li h6{
        padding-top: 10px;
    }
 
    
    .footer ul li #ul4 li h6{
        padding-top: 10px;
    }
    .footer ul li #ul1 {
        padding-top: 0;
    }
    .footer ul li #ul2 {
        padding-top: 0;
    }

    .footer ul li #ul3 {
        padding-top: 0;
    }
    .footer ul li #ul4 {
        padding-top: 0;
    }
    
}